import React, { useEffect, useState } from 'react'
import { Link } from "react-router-dom";
import {HomeFilled , PlaySquareFilled , RobotFilled, PictureFilled} from "@ant-design/icons";
import './NavBar.css'



const NavBar = ({apiCategory}) => {
    const [apiLink , setApiLink] = useState([])

    useEffect(() => {
        setApiLink(apiCategory)
    }, [apiCategory])


    return (
        <div className='nav-bar' >

            <ul className="nav-list">
                <Link to="/rickandmorty">
                    <HomeFilled />
                    <p>HOME</p>
                </Link>

                <Link to={`/rickandmorty/${apiLink[0]}`}>
                    <RobotFilled />
                    <p>CHARACTERS</p>
                </Link>

                <Link  to={`/rickandmorty/${apiLink[1]}`}>
                    <PictureFilled />
                    <p>LOCATIONS</p>
                </Link>

                <Link to={`/rickandmorty/${apiLink[2]}`}>
                    <PlaySquareFilled />
                    <p>EPISODES</p>
                 </Link>

            </ul>
        </div>
    )
}
export default NavBar
