import React, {useEffect, useState} from 'react'

import {useParams} from "react-router-dom";
import { IconId , IconUser, IconGenderBigender, IconHeartbeat , IconLungsOff , IconCell , IconMapPin , IconDna2} from '@tabler/icons-react';
import {getCharacters, getEpisode} from "../services/api";

import './CharacterInfo.css'

const CharacterInfo = () => {
    const [allInfoCharacters , setAllInfoCharacters] = useState([])
    const [episode , setEpisode ]= useState([])


   const  {page , id} = useParams()


    useEffect(()=>{
        getCharacters(page).then((res)=>{
            setAllInfoCharacters(res.results)
        })

    },[page])

    const  charactersInfo =  allInfoCharacters.filter((elem)=>{
        return elem.id == id
    })

    console.log(charactersInfo)
    return(
        <div className='info-block-character'>
            { charactersInfo.map((elem, i)=>{

               return (
                   <div className='info-block-character-container'  key={i}>
                       <img src={elem.image}/>
                       <div className='info-block-character-text'>
                           <p><IconUser />Name: {elem.name}</p>
                           <p><IconId />ID: {elem.id}</p>
                           <p><IconGenderBigender />Gender: {elem.gender}</p>
                           <p>{elem.status == "Dead" ? <IconLungsOff /> : <IconHeartbeat />}
                               Status: {elem.status}</p>
                           <p><IconCell />Species: {elem.species}</p>
                           <p><IconMapPin />Location: {elem.location?.name}</p>
                           <p><IconDna2 />Type: {elem.type.length ? elem.type : "Unknown"}</p>

                       </div>

                   </div>
               )

            })

            }
            <h4>Episode</h4>

        </div>
    )

}
export default CharacterInfo
