
import './App.css';
import React, {useState , useEffect} from 'react';
import {Routes, Route  } from 'react-router-dom'
import NavBar from "./Components/NavBar";
import axios from "axios";


import MainPage from "./pages/MainPage";
import NotFoundPage from "./pages/NotFoundPage"

import Character from "./pages/Character";
import CharacterInfo from "./pages/CharacterInfo";

import Locations from "./pages/Locations";
import Episodes from "./pages/Episodes";
import Search from "./Components/Search";
import {getCharacters} from "./services/api";
import RandomCharacters from "./Components/RandomCharacters";


const fullApi    = "https://rickandmortyapi.com/api"

const characters = "https://rickandmortyapi.com/api/character"

const episodes   = "https://rickandmortyapi.com/api/episode"


function App() {

    const [apiCategory , setApiCategory] = useState([]) // NAVBAR

    const [characters , setCharacters] = useState([]) // массив с персонажами 20шт
    const [lastPage , setLastPage] = useState(null) // последняя страница (42)
    const [loading, setLoading] = useState(true) // состояние загрузки
    const [pageCharacters, setPageCharacters] = useState([]) // ссылки на след и предыдущую страницу инфо о количестве:  1. Персонажей 2. Страниц
    const [mainPage, setMainPage] = useState(1)


    useEffect(()=>{
        axios
            .get(fullApi)
            .then(res => {
                setApiCategory(Object.keys(res.data))
            })
    },[])

    useEffect(() => {
        getCharacters(mainPage).then
        ((res) => {
            setCharacters(res.results)
            setLoading(res.loading)
            setPageCharacters(res.info)
            setLastPage(res.info.pages)

        });

    }, [mainPage]);



    return (
      <div className='wrapper'>

          <div className='main-page-container'>

              <Search characters = {characters}/>

              <div className='title-page'>

                  <NavBar apiCategory={apiCategory}/>

                  <div className='title-page-info'>

                      <Routes>

                          <Route path='/rickandmorty' element={<MainPage />} />
                          <Route path='/rickandmorty/characters' element={<Character />} />
                          <Route path='/rickandmorty/character/:page/:id' element={<CharacterInfo />} />
                          <Route path='/rickandmorty/locations' element={<Locations />} />
                          <Route path='/rickandmorty/episodes' element={<Episodes />} />
                          <Route path='*' element={<NotFoundPage />} />

                      </Routes>
                  </div>
              </div>
          </div>

      </div>

  );
}

export default App;
